.NewProjectForm-Form {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    background-color: var(--primary-bg);
    height: 40vh;
    width: 80vw;
    padding: 20px;
    color: var(--primary-text);
    text-align: center;
    margin-top: -20vh;
}

.NewProjectForm-Group > p {
    font-size: 20px;
}

.NewProjectForm-Group {
    margin: 10px;
}

.NewProjectForm-Group > input {
    width: 100%;
    height: 50px;
    font-size: 20px;
    border-radius: 5px;
    border: none;
    padding: 5px;
}

.NewProjectForm-Buttons { 
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.NewProjectForm-Buttons > .btn {
    padding: 15px;
    border-radius: 5px;
    text-decoration: none;
    text-align: center;
    width:140px;
    margin: 10px;
    color: var(--primary-text);
}

.NewProjectForm-Buttons > .default {
    background-color: var(--primary-green);
}

.NewProjectForm-Buttons > a.btn.cancel {
    background-color:var(--primary-red);
    display: inline-block;
}

@media only screen  and (min-width : 900px) {
    .NewProjectForm-Form {
        width: 50vw;
    }
}


