.HomePage-Swatches {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    border-radius: 5px;
    width: 100vw;
    padding: 10px;
    margin: -10px auto 0;
}

.HomePage-BigText {
    margin-left: 20px;
}

.HomePage-Swatch {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    height: 110px;
    width: 110px;
    margin: 0 5px 20px;
}

.HomePage-Swatch > .img {
    width: 100px;
    height: 100px;
}

.HomePage-Buttons {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
}

.HomePage-Buttons > .btn {
    padding: 15px;
    border-radius: 5px;
    text-decoration: none;
    text-align: center;
    width: 150px;
    margin: -10px auto 10px;
    color: var(--primary-text);
    background-color: var(--primary-bg);
    display: inline-block;
}

.Project-btn {
    padding: 15px;
    border-radius: 5px;
    text-decoration: none;
    text-align: center;
    width: 300px;
    margin: 10px auto;
    color: var(--primary-text);
    background-color: var(--primary-green);
    display: inline-block;
}

.NewProject-btn {
    padding: 15px;
    border-radius: 5px;
    text-decoration: none;
    text-align: center;
    width: 150px;
    margin: 0 auto 10px;
    color: var(--primary-text);
    background-color: var(--primary-bg);
    display: inline-block;
}

.HomePage > h2 {
    color: var(--primary-bg);
}

.HomePage > h2 > span {
    color: black;
}

@media only screen  and (min-width : 900px) {
    .Project-btn {
        width: 600px;
    }
}


